import { Formik } from 'formik';
import React from 'react';
import { Form } from 'semantic-ui-react';
import { TInputTypeObject, TOutputFormat, TReadOnlyTypeObject } from './utils/types/setupTypes';
import { IVocabulary } from '../interfaces';
import { showErrorToast } from '../../../utils/functionUtils';
import { SaveButtonValidation } from '../../shared/SaveButtonValidation';
import { RenderLayoutInput } from './renderers/RenderLayoutInput';
import { IPermitData, IUserInfo } from '../../../interfaces/ApiInterfaces';
import { useDyamicFormInitializer } from '../hooks/useDynamicFormInitializer';
import { generateLayoutSchema } from './utils/layoutSchemaGenerator';
import { RenderLayoutTable } from './renderers/RenderLayoutTable';
import { RenderReadOnlyData } from './renderers/RenderReadOnlyData';
import { ELayoutIdentifier } from './utils/enums/LayoutIdentifiers';
import { RenderStaticImage } from './renderers/RenderStaticImage';
import { RenderReadOnlyType } from './renderers/RenderReadOnlyType';
import { RenderComponents } from './renderers/RenderComponents';
import { EComponentIdentifiers } from './utils/enums/ComponentIdentifiers';

type TProps = {
	// data: string;
	// dataMapping: IFormFieldList[];
	inputTypes: TInputTypeObject[];
	readOnlyTypes: TReadOnlyTypeObject[];
	postAction: (data: any) => void;
	output: TOutputFormat[];
	loading: boolean;
	savePermission: boolean;
	applicationNo: string;
	prevData: any;
	readOnlyData: any;
	vocabulary: IVocabulary[];
	permitData: IPermitData;
	userData: IUserInfo;
	staticFiles: any;
	isEdit?: boolean;
	// setOutput: Dispatch<SetStateAction<TOutputFormat[]>>;
};

const floorDummyData = [
	{ floor: 1, length: 12, height: 41, width: 52, area: 523 },
	{ floor: 2, length: 12, height: 41, width: 52, area: 523 },
];

export const LayoutRenderer = ({
	applicationNo,
	prevData,
	readOnlyData,
	inputTypes,
	readOnlyTypes,
	output,
	postAction,
	loading,
	savePermission,
	vocabulary,
	permitData,
	userData,
	staticFiles,
	isEdit = false,
}: TProps) => {
	const { initialValues, getDataToSend } = useDyamicFormInitializer(inputTypes, prevData, permitData);

	return (
		<Formik
			enableReinitialize
			initialValues={initialValues}
			validationSchema={generateLayoutSchema(inputTypes)}
			onSubmit={async (values, { setSubmitting }) => {
				setSubmitting(true);

				const dataToSend = getDataToSend(values, applicationNo);
				try {
					await postAction(dataToSend);
					// if (this.props.success && this.props.success.success) {
					// 	handleSuccess(checkError(this.props.prevData), this.props.parentProps, this.props.success);
					// }
					window.scrollTo(0, 0);
					setSubmitting(false);
				} catch (err) {
					setSubmitting(false);
					window.scrollTo(0, 0);
					console.log('Error', err);
					showErrorToast('Something went wrong !!');
				}
				// setTimeout(() => {
				console.log(JSON.stringify(dataToSend, null, 2));
				//   setSubmitting(false);
				// }, 400);
				setSubmitting(false);
			}}
		>
			{(props) => (
				<Form loading={props.isSubmitting || loading}>
					<div style={{ minHeight: '500px' }}>
						<div>
							{output &&
								output.map((row) => (
									<React.Fragment key={row.id}>
										{row.input ? (
											<RenderLayoutInput
												content={row.content}
												formikProps={props}
												inputTypes={inputTypes}
												vocabulary={vocabulary}
												permitData={permitData}
											/>
										) : row.table ? (
											<RenderLayoutTable
												row={row}
												formikProps={props}
												readOnlyData={readOnlyData}
												inputTypes={inputTypes}
												vocabulary={vocabulary}
												userData={userData}
												permitData={permitData}
												staticFiles={staticFiles}
											/>
										) : row.type === ELayoutIdentifier.DATA ? (
											<RenderReadOnlyData vocabulary={vocabulary} row={row} readOnlyData={readOnlyData} />
										) : row.type === ELayoutIdentifier.USER_INFO ? (
											<RenderReadOnlyType row={row} prevData={prevData} readOnlyTypes={readOnlyTypes} />
										) : // <RenderUserInfo row={row} userData={userData} />
										row.type === ELayoutIdentifier.STATIC_IMAGE ? (
											<RenderStaticImage row={row} staticFiles={staticFiles} />
										) : row.type === ELayoutIdentifier.COMPONENT ? (
											<RenderComponents
												row={row}
												type={row.content as EComponentIdentifiers}
												userInfo={userData || { organization: {} }}
												permitData={permitData}
											/>
										) : (
											<span
												// style={{
												// 	display: row.inline ? 'inline-block' : 'inline',
												// }}
												dangerouslySetInnerHTML={{ __html: row.content }}
											/>
										)}
									</React.Fragment>
								))}
							{/* {formattedData && (
                )} */}
							{/* <div dangerouslySetInnerHTML={{ __html: formattedData }} /> */}
							{/* <Button
								primary
								content="Save"
								onClick={(e) => {
									e.preventDefault();
									props.handleSubmit();
								}}
							/> */}
						</div>
						<br />
						<SaveButtonValidation
							errors={props.errors}
							formUrl={''}
							hasSavePermission={savePermission}
							isEdit={isEdit}
							hasDeletePermission={false}
							isSaveDisabled={false}
							handleSubmit={props.handleSubmit}
							validateForm={props.validateForm}
						/>
					</div>
				</Form>
			)}
		</Formik>
	);
};
